import {
	mapState,
	mapMutations
} from 'vuex';
var graceChecker = require('@/common/graceChecker.js');
export default {
	data() {
		return {
			userName: '',
			code: '',
			second: 90,
			showText: true
		};
	},
	onLoad(option) {
		if (option.nvitationCode) this.nvitationCode = parseInt(option.nvitationCode);
	},
	onNavigationBarButtonTap(e) { //点击事件
		console.log("success-------------", e)
		uni.redirectTo({
			url: '/pages/member/login'
		})
	
		// 修改buttons    
		// index: 按钮索引, style {WebviewTitleNViewButtonStyles }    
		// webView.setTitleNViewButtonStyle(0, {    
		//       text: '完成',    
		// });
	},
	methods: {
					...mapMutations(['login']),
		sendCode() {
			//发送验证码
			var _this = this;
			if (_this.userName == '') {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}
		
			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.userName,
					type: 1
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {
						_this.showText = false;
						var interval = setInterval(() => {
							let times = --_this.second;
							_this.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							_this.second = 90;
							_this.showText = true;
						}, 90000);
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		dealLogin(data) {
			//处理登录
			this.login({
				id: data.id,
				id_card: data.id_card,
				id_card_name: data.id_card_name,
				information_state: data.information_state,
				user_pay_state: data.user_pay_state,
				invitation: data.invitation,
				openid: data.openid,
				phone: data.phone,
				pid: data.pid,
				token: data.token,
				user_fen: data.user_fen,
				user_head: data.user_head,
				user_name: data.user_name,
				user_type: data.user_type
			});

			uni.switchTab({
				url: '/pages/member/center'
			});
			// if (this.isBack) {
			// 	uni.navigateBack();
			// } else {
			// 	uni.switchTab({
			// 		url: '/pages/member/center'
			// 	});
			// }
		},


		formSubmit(e) {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: 'userName',
					checkType: 'phoneno',
					errorMsg: '电话号码不正确'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				}
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.msgLogin(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 200) {
							// uni.navigateTo({
							// 	url: '/pages/member/login'
							// });
							_this.dealLogin(data.data)
						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
